module.exports = {"fr":{"menu":{"map":"Carte","travels":"Voyages"},"map":{"back":"Retour","more":"Plus +"},"albums":{"peloponnese":{"displayName":"Pelopnnsos","text":"Septembre 2020 : On embarque pour une nouvelle anne d'aventure ! Premire tape : Trois mois en Grce dans le Ploponnse... <br> Le Ploponnse ou  c'est cette grande pninsule au Sud-Est du continent. Entre mer, montagnes et tltravail, on partage notre temps  explorer les fonds marins, escalder les falaises voisines, explorer les montagnes du coin, se balader dans les mignons petits villages et dvorer la gastronomie locale !","story":{"1":"Lundi 21 Septembre, aprs plus de 1000 km sur l'autoroute italienne et 19h en ferry, nous voila enfin en Grce ! Encore quelques heures de route et nous seront enfin dans notre nouveau chez nous !","2":"Une fois les valises dballes, premire balade  Lonidio () et premire visite du quartier...","4":"Notre petite maison au coeur de Leonidio, sous les falaises de Kokkinovrachos ( ) et  quelques kilomtres de la mer !  ","6":"Vue sur Leonidio, avec ces 3760 habitants la vie y est paisible  Le matin c'est l'glise, et le coq qui nous sortent du lit ! ","8":"Le monastre Elona  deux pas de Lonidio est perch sur une troite avance de la falaise.","10":"Le pope veille au grain dans les couloirs du monastre...","11":"Tous les soirs on enfourche les vlos et direction la plage de Plaka  5km !  Et on se dcouvre une nouvelle passion pour la plonge dans les grands fonds (au moins 4 mtres !) a observer les poissons","12":"Et c'est encore mieux quand les copains nous rejoignent pour dcouvrir les saveurs locales !  Sylvie & Matthieu !","13":"Premier week-end grque ! Direction Monemvasia () dans le sud du Peloponnese. Au programme dambulation dans la ville et exploration des plages de Plitra ().","15":"La ville fortifie est magnifique ! Fonde au Vie sicle, elle a conserv un charme fou !","20":"Petit apro depuis la plage et on a la chance de profiter d'un lever de Lune.","22":"Le week-end dernier on enfile les baskets pour une premire rando greque ! Direction le Megali Tourla ( ) le plu haut sommet du Parnon, il s'agit de la chaine de montagne visible depuis Leonidio. Ca grimpe tout de mme  1940m d'altitude !","23":" Heureusement que l'on a rvis notre alphabet grec avant de partir ;)","24":"On the top !  Mer & Montagne ! La rando a pas l'air d'tre le sport national dans le coin ! Les chemins sont trs bien baliss mais on a pas crois grand monde ^","25":"Et au retour un joli point de vue sur le Monastre de Panagia Malevi (  ). Y a pas  dire ils ont toujours du style les monastres grecques !","26":"Romaric profite de la quasi absence totale de lumire et de nuage pour mitrailler les toiles depuis ... la fentre de la chambre ! Sances photos toiles depuis l'intrieur c'est quand mme plutt confort !","27":"Samedi dernier, direction le Magne () pour un road trip le long de la cote.","29":"Ici Pas de petites maisons blanches aux coupoles bleues surplombant une mer dun bleu profond, pas dhtels de luxe mais des villages de pierre aux allures de forteresses, des paysages arides et sauvages, des troupeaux en libert, et des routes au milieu de rien. Si vous aimez conduire sur des routes sinueuses, offrant des points de vue  couper le souffle, sans tre embt par le trafic, le Magne est dfinitivement fait pour vous !","31":"Tout au Sud de la pointe la plus au Sud du Ploponnse : Tenaro ( )","33":"Ces tours, vritables symboles de la rgion, reprsentent lhritage dune poque (le XVIme sicle) o les Maniotes, avaient besoin de se protger des envahisseurs extrieurs.","35":"Sur la route direction Ermioni (), les champs d'oliviers  perte de vue...","37":"Aprs une courte traverse en bateau, on dbarque  Hydra (). Cest la seule le grecque sur laquelle on ne trouve aucun vhicule  moteur (hormis le camion-poubelle). Ni voiture ni scooter !","38":"Premire mission ? S'engoufrer dans les petites ruelles derrire le port, s'y perdre et croiser le millier de chat qui ronronnent le long des rues. Ce sont dfinitivement eux les vrais Pachas sur l'le !","41":"Le port dHydra est un savant mlange de des petites barques, de bateaux de pcheurs, de gros yachts, de mulets qui patientent, et les chats qui attendent le retour des pcheurs...","44":"On enfile les baskets, direction le Mont Eros le point culminant de l'le, pour un panoama  360 degrs !","49":"Dernier coucher de soleil... Bye bye Hydra !","50":"Vendredi, on prend la route direction Sparte ! Au programme la visite de Mistra () une ancienne cit fonde par les Francs au xiiie sicle. Elle est aujourd'hui en ruines !","51":"Moulte fois dtruite et recontruite, seuls les monastres et les glises tiennent encore debout. Mais comptez quand mme trois heures de visite, il y en a beaucoup, beaucoup, beaucoup ...","56":"Dpart  pied de la maison pour une balade sur les hauteurs de Leonidio.","57":"On voulait dcouvrir l'endroit depuis quelques semaines dj, et l'on a pas t du ! Une journe  Kyparissi () petit village dans un crin de verdure, avec des centaines de voies d'escalade  explorer ! On a ador, et l'on reviendra ;)","58":"Sur le chemin du retour, petit arrt  Fokiano, pour un caf et un chocolat face  la mer.","59":"Dimanche dernier, on prend une nouvelle fois la route, pour une journe visite des sites antiques ! Premire tape Mycnes (), une cit antique prhellnique.","60":"La porte des Lionnes est l'entre principale de la citadelle de Mycnes. Elle a t rige au XIIIe sicle avant notre re, vers 1250 av. J.-C. (!!!) et tient (toujours debout) son nom de la sculpture reprsentant deux lionnes places au-dessus de la porte.","63":"A quelques kilomtres de Mycnes, nous dcouvrons le Thatre d'Epidaure ! Remarquablement conserv, il est ddi  l'ancier dieu grec de la mdecine, Asclpios.","64":"C'est grandiose ! Le Thatre peut accueilir jusqu' 12000 personnes, l'acoustique du thtre est justement renomme. Le moindre son produit au bas des gradins se propage jusqu'aux ranges suprieures. On a test c'est impressionnant !","65":"Le Sanctuaire d'Asclpios date du IVe sicle av. J.-C.. Durant l'Antiquit, les plerins accouraient de toute la Grce pour se faire soigner dans le sanctuaire du dieu gurisseur.","68":"J-1 avant le confinement ! Ni une ni deux, on monte dans la voiture direction le Nord ! Premier arrt L'Acrocorinthe (), une citadelle antique dfendant l'isthme de Corinthe depuis le vie sicle av. J.-C... La place tait  priori stratgique, puisque occupe successivement par les Corinthiens, Romains, Byzantins, Francs, Vnitiens et Ottomans... Un joyeux mlange culturel que l'on visite encore une fois compltement seuls !","71":"Pour rejoindre le continent (et quitter pour la toute premire fois le Ploponnse... pour quelques heures seulement !), on passe par le mythique Canal de Corinthe (  ) ! 6km de long, 24m de large, c'est impresionnant ! Ds le viie sicle av. J.-C., les Bacchiades avaient reli le golfe Saronique au golfe de Corinthe par un...chemin de bois pour le roulage des navires ! Il faudra attendre 1893 pour que les travaux entrepris 11 ans plus tot vitent enfin aux navires un dtour de 400 km autour de la pninsule du Ploponnse !","73":"Derniers stops avant de rentrer  la maison : Le 'lac de Vouliagmenis' (qui n'en est pas vraiment un ! Un petit canal permet en fait  l'eau de mer de rentrer dans le Golf !) et le site antique Heraion (   )...","74":"...pour une dernire bouffe d'air frais et une balade sur les hauteurs !","75":"Confinement oblige, on reste dans le proche voisinage de la maison, heureusement la mer est accessible ! On s'y est mme baign ce weekend ! Pour un 15 novembre, elle tait plutt bonne !","76":"Sur le port, Romaric fait connaissance avec son nouveau copain chat.","77":"On profite de ces journes toujours aussi ensoleilles pour arpenter les chemins autour de Leonidio, et dcouvrir les spots d'eslcalade de fou ! Ici le secteur 'Mars' qui tient son nom de la couleur orange du rocher et de la difficult des voies rserves  quelques ... extraterrestres ! ","80":"Les champs d'orangers et de mandariniers ont pris de la couleur depuis quelques jours ! C'est beau et c'est bon !","81":"Fin du week-end par une belle grande voie au-dessus de Lonidio (encore !), le temps et le rochers sont splendides, on rentre en stop berc par la musique traditionnelle grecque... Que demander de plus ? ;)","83":"Le confinement est toujours de mise et nous en profitons pour visiter les alentours de Leonidio pour dcouvrir de nouveaux points de vues : ici depuis la chapelle :   (La bastille Grenobloise de ) :) !","85":"L'hiver arrive, a se sent, la pluie tombe aussi dans le Ploponnse, les oliviers ont enfin de l'eau aux pieds. :)","87":"La balade du jour nous promne sur les hauteurs. Objectif : Rejoindre la chapelle de Saint Athanasios. On profite de la vue imprenable sur la mer et la plage de Poulithra ().","91":"En fin de journe, on s'embarque dans un mini road trip, direction le tout petit village de Paleochori. Ici pas un chat, le village semble presque endormi... L'automne est maintenant bien install ! Et les arbres prennent des couleurs ! ","92":"Photo prise depuis le haut d'une voie d'escalade dans un de nos endroits ftiches pour grimper !","93":"Rando un jour, rando toujours... Cette fois-ci pour tenter de rejoindre le village de Voskina ().","94":"Ca y est il est l'heure de dire au revoir  notre maison adore de Lonidio pour de nouvelles aventures :)","95":"On se met en mode hiver avec notre nouveau petit chez nous pour fter Nol !","97":"On profite de la terasse pour admirer les couchers soleil tous les aprs-midi.","99":"Douverte de la ville de Naupacte () et de sa forteresse qui connu de nombreux assauts de la part des Italiens..","101":".. en finissant par la visite de son jolie petit port","106":"A l'ascension du mont Varasova () qui est notre terrain jeu de grimpe depuis le dbut des vacances. Du haut de ses 917m, nous aurons au sommet le droit  une vue nuagueuse dans une ambiance Nolique, h oui on est le 23 dcembre :)"}},"thessalia":{"displayName":"Epirus & Thessalia","text":"Aprs 3 mois dans le Peloponnse on met les voiles vers les Nord direction l'Epire et la Thessalie ! On s'installe pendant presque deux mois  Kalambaka ou  au pied des Mtores ! L'objectif ? Grimper videmment ces normes pitons rocheux sculpts par l'rosion et qui semblent tout droit tombs du ciel. Mais aussi randonner dans les parcs nationaux du coin : le Parc de Vikos  Aoos, et ses gorges impressionnantes et le Parc de Tzoumerka avec ces sommets enneigs. C'est parti pour l'aventure !","story":{"1":"Avant d'arriver  Kalambaka (), on pose nos valises  Rodotopi () un petit village  proximit de Ioannina () et de son lac surtout clbre pour son le qui servit de refuge  Ali Pacha, lors du conflit qui opposa la rgion aux forces Ottomanes.","2":"Aprs 4 jours de dluge, le soleil pointe enfin le bout de son nez ! Ni une ni deux on monte dans la voiture direction les Gorges de Vikos au cur du pays de Zagoria (qui signifie 'au-del des montagnes'). Vikos est mme class dans le livre des records comme le canyon le plus profond du monde par rapport  sa largeur ! Et l'on ne va pas tre du !","3":"On colle la paroi sur le petit chemin de 'promenade' qui serpente le long de la falaise ! Pas moins de 900m de vide s'tendent jusqu' la rivire au fond du canyon.","9":"Hh la neige grecque pointe le bout de son nez...","10":"Clbres dans la rgion les petits ponts de pierre sont nombreux dans le coin. Ils datent du XVIIime sicle, et font jusqu' 50m de long.","17":"Les balades et points de vue depuis le haut des falaises nous auront donn un avant got de la grandeur des gorges ! Mais la seule faon d'y pntrer et d'explorer le canyon se fait via le chemin de randonne au fond qui suit la rivire. Le rendez-vous est pris pour ce printemps !","18":"Pour cette seconde journe de soleil (du jamais vu dans la rgion !) on roule direction le sud, et le parc national de Tzoumerka ! Au programme : des monts enneigs culminant  plus de 2000m et une pointe de nostalgie en pensant  nos montagnes grenobloises...","19":"Le long des routes grecques on aperoit trs souvent des petites maisonnettes avec une petite bougie ou des icnes  l'intrieur. Appeles 'afiroma', il s'agit en fait de petites chapelles qui tmoignent de la mmoire des dfunts ou sont riges en remerciement d'un dcs vit lors d'un accident. Sympa !","22":"On est encore une fois tout seul le long des chemins de rando ( cause du confinement gnral toujours en vigueur en Grce peut-tre ?!), et les paysages sont incroyables ! Qui croirait qu'il s'agit de la Grce ?!","24":"Avant de quitter le parc, un dernier arrt au monastre de Kipinas (  n) qui semble fusionner avec la roche... Malheureusement pas possible de le visiter en ce moment.","25":"Nouvelle maison, nouvelle vue ! Welcome dans les Mtores et plus prcisment  Kalambaka () o nous avons trouv racine jusqu' fin fvrier.","27":"Les Mtores () sont une formation gologique du nord de la Grce, en Thessalie, dans la valle du Pne. Ils abritent des monastres chrtiens orthodoxes ( ,  monastres suspendus au ciel ) perchs au sommet de falaises et pitons rocheux gris, sculpts par l'rosion. Le site est inscrit au patrimoine mondial de l'UNESCO depuis 1988.","30":"Les monastres sont perchs dans les falaises dtritiques, composes de  poudingue  (conglomrat form de galets lis par un ciment sableux) qui surplombent au nord la petite ville de Kalambaka.","31":"On en profite pour attaquer notre premire grande voie du coin, avec ce rocher particulier que nous n'avons jamais grimp jusqu'ici. Les points sont espacs, mais la vue est grandiose il y a de l'ambiance tout en haut de l'Ambaria ! <3","32":"Tradition oblige, toutes les grandes voies du coin se termine par un petit carnet mis  disposition tout en haut afin que chacun puisse y crire son petit mot en signe de passage, c'est chose faite pour nous aussi ! ;)","35":"Entre midi et deux, le soir aprs le boulot, ou le week-end, toutes les occasions sont bonnes pour dcouvrir les Mtores ! Des dizaines de kilomtres de sentiers sont en plus baliss, et  pied sans personne c'est carrment grandiose !","36":"Ambiance frique quand la neige recouvre les monastres !","41":"Pour accder aux nombreux monastres, il faut grimper de nombreuses marches, ou alors... monter dans les mini tlphriques qui transportent personnes et ravitaillements !","46":"Ce dimanche on prend de la hauteur, et on grimpe jusqu' Koromilia (). Ce petit village, perch au-dessu de Kalambaka (compltement dsert en hiver) est le point de dpart pour une jolie rando qui monte au sommet du mme nom  1650m. La neige tombe le week-ed dernier a bien tenue, et l'on profite de ces paysages d'hiver en plein coeur de la Grce !","48":"Tout au fond les Mtores, ils ont l'air tout petits vu d'ici !","50":"En route pour le Lac Plastiras, qu'on pourrait nommer 'La Suisse Grecque' ! L'accs est pour le moins scabreux, car les violents orages du coin ont eu des effets dsastreux sur les routes de la valle. On zig-zag donc entre les normes affaissements de terrain et rochers pour atteindre un peu par hasard ce magnifique point de vue, que ce soit ct Est ou Ouest ;)","51":"Ca c'est le ct Est !","52":"L't arrive et la grimpette se fait de plus en plus rcurrente devant ce caillou merveilleux et bien sec !","54":"Le repos de la guerrire au sommet !","55":"H voil qui on croise au dtour d'un mtore  bronzer au soleil :D","56":"Grosses chutes cette nuit, le balcon n'est plus utilisable.. mais la vue prend une tout autre forme !","57":"All on remonte au sommet pour admirer encore et toujours les Mtores et ses Monastres, on ne s'en lasse pas.","58":"Dernire petite photo souvenir pour notre dernier jour, direction le Pelion !"}},"magnesia":{"displayName":"Magnesia","text":"Les 2 mois aux Mtores ont t formidables, mais il tait temps pour nous de quitter les montagnes du nord de la Grce et de retrouver la mer et son eau bleue ! On s'intalle pour un petit mois dans le Pelion dans un petit village dnomm Agria (), situ tout prs de la grande ville de Volos (). Pas de grimpe ici, mais de sublimes paysages et plages !","story":{"1":"Premire soire dans le Pelion et notre premier coucher de soleil depuis le balcon. On retrouve les palmiers, les ovliers et citronniers grecs !","2":"Sur les quais du port, les chiens (toujours aussi nombreux) se prlassent au soleil ... Le printemps est bien de retour ! ","3":"Premier week-end dans le Plion, on s'embarque dans un petit road trip direction la pointe sud de la presqu'le. Les plages de galets, les petites criques et les ports de pcheurs s'enchainent.","8":"Ce matin au programme : Randonne sur les hauteurs du Plion !  1600m la neige est bien l, et on voit la mer tout en bas !","9":"Au sommet surprise... Mlange de base militaire laisse  l'abandon et de d'antennes de diffusion, ambiance base secrte ... On se demande si on a vraiment le droit d'tre ici ?","10":"Et l'aprs-midi, direction la plage pour notre premire baignade de l'anne ! L'eau est gele ! Mais qu'est ce que a fait du bien !"}}},"loader":{"0":"Egouttage de la feta","1":"Pressage de l'huile d'olive","2":"Prparation du saganaki","3":"Elaboration de la salade grecque","4":"Confection du tzatziki","5":"Cuisson de la moussaka","6":"Prparation d'un caf frapp","7":"Dgustation de l'ouzo"}}}

